.main-nav {
  display: grid;
  grid-template-columns: 1fr max-content;
  column-gap: 20px;
  align-items: center;
  padding-top: 35px;
  padding-bottom: 32px;
  padding-left: 8.3vw;
  padding-right: 8.3vw;
  background-color: #ffffff;

  @media (max-width: 1380px) {
    align-items: end;
  }

  @media (max-width: 580px) {
    grid-template-columns: 1fr;
  }
}

.main-nav__glass {
  display: block;
  max-width: 108px;
  height: 44px;
  margin-left: auto;

  @media (max-width: 1080px) {
    max-width: 70px;
    height: 28px;
    height: 35px;
  }

  @media (max-width: 1280px) {
    max-width: 86px;
  }

  @media (max-width: 580px) {
    margin: 0 auto;
    margin-top: 13.7vw;
  }
}

.main-nav__toggle {
  display: none;
}
