.study-content {
  display: grid;
  grid-template-columns: 23.5vw 1fr;
  column-gap: 3.9vw;
  padding-top: 42px;
  padding-bottom: 5.7vw;

  @media (max-width: 1080px) {
    grid-template-columns: 1fr;
  }
}

.study-content.container {
  padding-left: 30px;
  padding-right: 5vw;

  @media (max-width: 480px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}
