.life-slider.swiper-container {
  position: relative;
  width: 42vw;
  touch-action: pan-y;

  @media (max-width: 1080px) {
    width: 89vw;
  }
}

.life-slider__list.swiper-wrapper {
  width: 100%;
  overflow: hidden;
}

.life-slider__item.swiper-slide {
  max-width: 100%;
}

.life-slider__item img {
  @media (max-width: 1080px) {
    width: 100%;
  }
}

.life-slider__item p {
  width: 100%;
  min-height: 80px;
  margin-top: 1.77vw;
  margin-bottom: 0;
  padding-right: 13vw;
  background-color: #ffffff;

  @media (max-width: 1080px) {
    margin-left: 0;
  }

  @media (max-width: 680px) {
    padding-right: 0;
  }
}

.life-slider__toggle {
  position: absolute;
  width: 50px;
  height: 37px;
  bottom: 0;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 10;

  @media (max-width: 1280px) {
    background-size: 80% auto;
  }

  @media (max-width: 1080px) {
    bottom: -10px;
  }

  @media (max-width: 767px) {
    display: none;
  }
}

.life-slider__toggle--prev {
  left: 80%;
  background-image: url("../img/arrow-slider.png");
  transform: scaleX(-1);

  @media (max-width: 1600px) {
    left: 76%;
  }

  @media (max-width: 1080px) {
    left: 84%;
  }
}

.life-slider__toggle--next {
  right: 0;
  background-image: url("../img/arrow-slider.png");
}

.life-slider__toggle:active {
  opacity: 0.5;
}
