.headline {
  font-family: "Minion-Pro", "Times", serif;
  font-size: clamp(65px, 4.7vw, 100px);
  font-weight: 700;
  color: #a46c2d;
  margin: 0;

  @media (max-width: 992px) {
    font-size: 60px;
  }

  @media (max-width: 680px) {
    font-size: 40px;
  }
}

.headline::first-letter {
  text-transform: uppercase;
}
