.life {
  padding-top: 2vw;

  @media (max-width: 1080px) {
    padding-top: 0;
  }
}

.life h3 {
  margin-bottom: 2.5vw;
}

.life__caption {
  margin: 0;
  margin-top: 5.99vw;

  @media (max-width: 1080px) {

  }
}

.life__caption li {
  border-bottom: 2px solid #cecece;
}

.life__caption li:last-child {
  border-bottom: none;
}

.life__caption p {
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 23px;
  padding-bottom: 23px;
  margin: 0;

  @media (max-width: 580px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}
