.site-nav {
  display: grid;
  grid-template-columns: repeat(4, max-content);
  column-gap: 2.19vw;
  row-gap: 20px;
  padding-left: 20px;
  padding-right: 20px;

  @media (max-width: 1380px) {
    grid-template-columns: repeat(2, max-content);
    column-gap: 50px;
  }

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }

  @media (max-width: 580px) {
    row-gap: 30px;
    text-align: center;
    padding-left: 0;
    padding-right: 0;
  }
}

.site-nav li {
  position: relative;
  font-family: inherit;
  font-size: clamp(16px, 1.1vw, 23px);
  line-height: 25px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: -1px;

  @media (max-width: 1380px) {
    font-size: 23px;
  }

  @media (max-width: 1280px) {
    font-size: 20px;
  }

  @media (max-width: 1080px) {
    font-size: 18px;
  }

  @media (max-width: 992px) {
    font-size: 25px;
    line-height: 28px;
  }

  @media (max-width: 680px) {
    font-size: 20px;
  }
}

.site-nav a {
  display: block;
  color: #000000;
  text-decoration: none;
  padding: 10px;
}

.site-nav li::before,
.site-nav li:last-child::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  top: 50%;
  transform: translateY(-50%);
  background-color: #000000;
  border-radius: 50%;
}

.site-nav li::before {
  left: -1.3vw;

  @media (max-width: 1080px) {
    width: 10px;
    height: 10px;
    left: -22px;
  }

  @media (max-width: 680px) {

  }

  @media (max-width: 580px) {
    top: 125%;
    left: 50%;
    transform: translateX(-50%);
  }
}

.site-nav li:first-child::before {
  @media (max-width: 580px) {
    top: 125%;
  }
}

.site-nav li:last-child::after {
  right: -22px;

  @media (max-width: 1380px) {
    display: none;
  }
}

.site-nav__item--active a {
  color: #ffffff;
  background-color: #000000;
}
