.sidenav {
  @media (max-width: 1080px) {
    width: 85%;
    padding-left: 5.2vw;
    margin-bottom: 4.6vw;
  }

  @media (max-width: 580px) {
    padding-left: 0;
    margin-bottom: 35px;
   }
}

.sidenav li {
  border-bottom: 2px solid #000000;
}

.sidenav li:last-child {
  border-bottom: none;
}

.sidenav a {
  display: block;
  font-family: inherit;
  font-size: 21px;
  font-size: clamp(14px, 1.1vw, 23px);
  line-height: 27px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
  letter-spacing: -1px;
  text-decoration: none;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 23px;
  padding-bottom: 23px;
  margin: 0;

  @media (max-width: 1280px) {
    font-size: 23px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
  }

  @media (max-width: 767px) {
    padding-top: 15px;
    padding-bottom: 15px;
  }

  @media (max-width: 680px) {
    font-size: 20px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

a.sidenav__link-active {
  font-weight: 700;
  color: #ffffff;
  background-color: #000000;
}
