.content-inner {
  position: relative;
}

.content-inner h1 {
  font-family: "Minion-Pro", "Times", serif;
  font-size: clamp(65px, 4.7vw, 100px);
  line-height: 75px;
  font-weight: 700;
  color: #000000;
  margin: 0;

  @media (max-width: 1080px) {
    line-height: 60px;
  }

  @media (max-width: 992px) {
    font-size: 60px;
    line-height: 50px;
  }

  @media (max-width: 680px) {
    font-size: 40px;
    line-height: 38px;
    text-align: center;
  }
}

.content-inner h1::first-letter {
  text-transform: uppercase;
}

.content-inner h1 > span {
  display: block;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: clamp(28px, 1.7vw, 34px);
  font-weight: 700;
  letter-spacing: -1.8px;
  color: #000000;
  font-variant: normal;
  margin-top: 50px;

  @media (max-width: 1080px) {
    font-size: 28px;
    line-height: 32px;
  }

  @media (max-width: 767px) {
    font-size: 25px;
    line-height: 30px;
    letter-spacing: -0.8px;
  }

  @media (max-width: 680px) {
    text-align: left;
  }

  @media (max-width: 580px) {
    font-size: 21px;
    line-height: 26px;
  }
}

.content-inner__subtitle {
  font-family: inherit;
  font-size: clamp(34px, 2vw, 40px);
  font-weight: 300;
  color: #000000;
  margin: 0;
  margin-top: 40px;
  margin-bottom: 45px;

  @media (max-width: 480px) {
    margin-top: 28px;
    margin-bottom: 30px;
  }
}

.content-inner p {
  margin: 0;
  margin-bottom: 1.8vw;

  @media (max-width: 680px) {
    margin-bottom: 18px;
  }
}

.content-inner--document p,
.content-inner--questions p,
.content-inner--teacher p {
  margin-bottom: 0;
}


.content-inner__marker-list {
  list-style-type: disc;
  list-style-position: inside;
  margin-bottom: 35px;
}

.content-inner__number-list {
  list-style-position: inside;
  padding-left: 0;
}

.content-inner__list ul {
  margin-top: 47px;

  @media (max-width: 480px) {
    margin-top: 20px;
  }
}

.content-inner__list ul {
  margin-bottom: 55px;

  @media (max-width: 767px) {
    margin-top: 30px;
    margin-bottom: 35px;
  }

  @media (max-width: 480px) {
    margin-top: 25px;
    margin-bottom: 30px;
  }
}

.content-inner__list p {
  font-family: inherit;
  font-size: clamp(34px, 2vw, 40px);
  line-height: 42px;
  letter-spacing: -1.5px;
  color: #000000;

  @media (max-width: 767px) {
    font-size: 25px;
    letter-spacing: -0.8px;
    margin-bottom: 30px;
  }

  @media (max-width: 580px) {
    font-size: 25px;
    line-height: 27px;
  }

  @media (max-width: 480px) {
    margin-bottom: 20px;
  }
}

.content-inner__list {
  margin-top: 55px;
}

.content-inner__list li,
.content-inner__list a {
  position: relative;
  display: block;
  font-size: clamp(25px, 1.6vw, 30px);
  line-height: 36px;
  color: #000000;
  letter-spacing: normal;
  padding-left: 90px;
  margin: 0;

  @media (max-width: 767px) {
    line-height: 27px;
  }

  @media (max-width: 580px) {
    font-size: 21px;
    line-height: 24px;
    padding-left: 50px;
  }
}

.content-inner__list a {
  padding-left: 0;

  &:visited {
    color: #545453;
  }
}

.content-inner__list li::before {
  position: absolute;
  content: "";
  width: 40px;
  height: 38px;
  left: 28px;
  background-image: url("../img/star.svg");
  background-repeat: no-repeat;

  @media (max-width: 580px) {
    left: 0;
    background-size: 75% auto;
  }
}

.content-inner--questions .content-inner__list li::before {
  width: 36px;
  height: 36px;
  background-image: url("../img/list.svg");

  @media (max-width: 580px) {
    left: 0;
    background-size: 75% auto;
  }
}

.content-inner--document .content-inner__list li {
  margin-bottom: 35px;
}

.content-inner--document .content-inner__list li::before {
  position: absolute;
  content: "";
  width: 40px;
  height: 40px;
  left: 28px;
  background-image: url("../img/pdf.svg");
  background-repeat: no-repeat;

  @media (max-width: 580px) {
    left: 0;
    background-size: 75% auto;
  }
}

.content-inner h1,
.content-inner h2,
.content-inner h3,
.content-inner h4,
.content-inner p,
.content-inner a {
  color: #000000;
}

.content-inner .content-inner__list li::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  border-radius: 50%;
  top: 5px;
  left: 28px;
  background: #000000;

  @media (max-width: 580px) {
    left: 0;
  }
}
