.form-search {
  position: relative;
  max-width: 400px;
  display: flex;
  align-items: center;
  padding: 3px;
  border: 0.7px solid var(--basic-white);

  @media (max-width: 767px) {
    width: 100%;
    margin: auto;
    margin-top: 50px;
  }

  @media (max-width: 580px) {

  }
}

.form-search input[type="search"] {
  width: 90%;
  font-size: 12px;
  color: var( --basic-white);
  padding: 0 0 0 10px;
  background: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
  pointer-events: auto;
}

.form-search input[type="search"]::-webkit-input-placeholder {
  font-size: 14px;
  color: #ffffff;
}

.form-search:focus-within input[type="search"],
.form-search input[type="search"]:valid {
}

.form-search button {
  width: 32px;
  height: 32px;
  background: url("../img/search.svg") transparent;
  background-repeat: no-repeat;
  background-size: cover;
  border: none;
  cursor: pointer;
  padding: 0;
}

.form-search input[type="search"]::-webkit-input-placeholder {
  font-family: inherit;
  color: #ffffff;
}

.form-search:focus input[type="search"]::-webkit-input-placeholder,
.form-search:hover input[type="search"]::-webkit-input-placeholder {
  visibility: visible;
}

.form-search:focus input[type="search"],
.form-search:hover input[type="search"] {
  visibility: visible;
}
