.preview-content__wrap {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 7vw;
  padding-top: 48px;
  padding-bottom: 29px;

  @media (max-width: 1080px) {
    grid-template-columns: 1fr;
  }
}

.preview-content__img {
  width: 100%;
  object-fit: cover;
}

.preview-content__wrap h2 {
  margin-bottom: 1.9vw;

  @media (max-width: 680px) {
    text-align: center;
  }

  @media (max-width: 480px) {
    line-height: 38px;
    margin-bottom: 30px;
  }
}

.preview-content__wrap h3 {
  font-size: clamp(50px, 3.9vw, 75px);
  line-height: 55px;
  margin-bottom: 1.9vw;

  @media (max-width: 1080px) {
    font-size: 48px;
  }

  @media (max-width: 680px) {
    font-size: 40px;
    text-align: center;
  }

  @media (max-width: 480px) {
    line-height: 38px;
    margin-bottom: 30px;
  }
}

.preview-content__wrap h4 {
  font-size: clamp(45px, 4vw, 67px);
  line-height: 50px;

  @media (max-width: 1080px) {
    font-size: 43px;
  }

  @media (max-width: 680px) {
    font-size: 36px;
    text-align: center;
  }

  @media (max-width: 480px) {
    line-height: 38px;
    margin-bottom: 30px;
  }
}

.preview-content__wrap h2::first-letter,
.preview-content__wrap h3::first-letter,
.preview-content__wrap h4::first-letter {
  text-transform: uppercase;
}

.preview-content__wrap p {
  margin-bottom: 25px;
}

.preview-content__item  {
  margin-bottom: 69px;

  @media (max-width: 767px) {
    margin-bottom: 45px;
  }
}

.preview-content__list:nth-child(2) .preview-content__item {
  @media (max-width: 580px) {
    margin-bottom: 0;
  }
}

.preview-content__list p {
  font-family: inherit;
  font-size: clamp(25px, 1.6vw, 30px);
  color: #000000;

  @media (max-width: 992px) {
    font-size: 25px;
  }

  @media (max-width: 480px) {
    font-size: 21px;
  }
}

.preview-content__list a {
  color: #000000;
}

.preview-content__list .caption {
  font-family: inherit;
  font-size: clamp(30px, 2vw, 34px);
  line-height: 36px;
  font-weight: 700;
  font-variant: small-caps;

  @media (max-width: 1080px) {
    font-size: 28px;
  }

  @media (max-width: 580px) {
    line-height: 26px;
  }
}

.preview-content h1,
.preview-content h2,
.preview-content h3,
.preview-content h4,
.preview-content p,
.preview-content a {
  color: #000000;
}
