.form-user {
  margin-top: 3.23vw;
}

.form-user > p {
  padding-right: 23vw;

  @media (max-width: 767px) {
    padding-right: 0;
  }
}

.form-user__filter {
  max-width: 872px;
  margin-top: 48px;
}

.form-user__field {
  margin-bottom: 20px;
}

.form-user__filter input,
.form-user__filter textarea {
  font-family: inherit;
  font-size: clamp(20px, 1.5vw, 25px);
  color: #000000;
  border: 1px solid #000000;
  padding-left: 18px;
  padding-right: 18px;
  padding-top: 16px;
  padding-bottom: 16px;
}

.form-user__filter input {
  width: 623px;

  @media (max-width: 680px) {
    width: 100%;
  }
}

.form-user__filter textarea {
  width: 100%;
  min-height: 282px;
  resize: vertical;
}

.form-user__filter textarea::placeholder {
  color: #000000;
}

p.form-user__note {
  margin-top: 2vw;
  margin-bottom: 2.6vw;
}

.form-user input[type="submit"] {
  display: block;
  width: 285px;
  height: 70px;
  font-family: inherit;
  font-size: clamp(20px, 1.5vw, 25px);
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #000000;
  border: none;
  margin-left: auto;
  padding: 12px;
  cursor: pointer;

  @media (max-width: 580px) {
    width: 100%;
  }
}
